
.app {
  max-width: 400px;
  margin: 40px auto;
  padding: 24px;
  background: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  text-align: center;
}

.game-controls button {
  margin: 4px;
  padding: 8px 16px;
  font-size: 0.9em;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
  transition: background 0.2s;
}

.game-controls button:hover:not(:disabled) {
  background: #e0e7ff;
}

.game-controls button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.question-section {
  margin-bottom: 20px;
}

.question-text {
  font-size: 1.2em;
  margin-bottom: 16px;
}

.options button {
  display: block;
  width: 100%;
  margin: 8px 0;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 6px;
  background: #fff;
  cursor: pointer;
  transition: background 0.2s;

}
.options button:hover {
  background: #e0e7ff;
}
.options button.correct {
  background: #bbf7d0;
  border-color: #22c55e;
  color: #166534;
}
.options button.wrong {
  background: #fecaca;
  border-color: #ef4444;
  color: #991b1b;
}

.result-feedback {
  margin-top: 16px;
  font-size: 1.1em;
}
.correct-msg {
  color: #16a34a;
  font-weight: bold;
}
.wrong-msg {
  color: #dc2626;
  font-weight: bold;
}

.score-section {
  font-size: 1.3em;
}
.score-section button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 6px;
  border: none;
  background: #6366f1;
  color: #fff;
  cursor: pointer;
  transition: background 0.2s;
}
.score-section button:hover {
  background: #4f46e5;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
